import { objToFormData, objToUrlParam } from './utils/object';

const contentTypes = {
  json: 'application/json; charset=UTF-8',
  urlencoded: 'application/x-www-form-urlencoded; charset=UTF-8',
  formData: 'multipart/form-data',
};

const defOptions = {
  method: 'POST', // POST || GET || PUT || DELETE
  contentType: contentTypes.urlencoded,
};

export const request = (url, body = {}, options = {}) => {
  const { method, contentType } = { ...defOptions, ...options };
  let b;
  if (contentType === contentTypes.json) {
    b = JSON.stringify(body);
  } else if (contentType === contentTypes.urlencoded) {
    b = objToUrlParam(body);
  } else if (contentType === contentTypes.formData) {
    b = objToFormData(body);
  }
  return fetch(url, {
    method,
    headers: {
      'Content-Type': contentType,
    },
    body: b,
  }).then((res) => res.json());
};

export const requestJson = (url, body) => request(url, body, { contentType: contentTypes.json });
