// 检查APP
import {useCallback, useEffect, useState} from 'react';
import {Modal, Toast} from '@ant-design/react-native';
import { checkApp } from '../common/appApi';
import {Linking} from "react-native";

const useAppCheck = () => {
  const [isNewVersion, setIsNewVersion] = useState(null);

  const openURL = useCallback(async (url) => {
    const supported = await Linking.canOpenURL(url);

    if (supported) {
      await Linking.openURL(url);
    } else {
      Toast.fail('下载失败，url出错');
    }
  }, []);

  useEffect(() => {
    checkApp().then((res) => {
      const { code, data } = res;
      if (code === 0) {
        const {
          buildHaveNewVersion, // 是否有新版本     Boolean
          buildVersion, // 版本号          String
          buildUpdateDescription, // 应用更新说明     String
          downloadURL, // 应用更新说明     String
        } = data;
        if (!buildHaveNewVersion) {
          Modal.alert(`发现新版本 ${buildVersion}`, buildUpdateDescription, [
            {
              text: '关闭',
              onPress: () => setIsNewVersion(buildHaveNewVersion),
              style: 'cancel',
            },
            {
              text: '更新',
              onPress: async () => {
                await openURL(downloadURL);
                setIsNewVersion(buildHaveNewVersion);
              },
            },
          ]);
        }
        console.log('res', res, buildHaveNewVersion);
      }
    });
  }, []);

  return isNewVersion;
};

export default useAppCheck;
