import React, {
  useCallback, useContext, useEffect, useState,
} from 'react';
import {
  Linking,
  SafeAreaView,
  ScrollView,
  StatusBar,
  StyleSheet,
  Text,
  useColorScheme,
  View,
} from 'react-native';
import { Button, Toast } from '@ant-design/react-native';

import {
  Colors,
  Header,
} from 'react-native/Libraries/NewAppScreen';
import { AppContext } from '../../common/AppContext';
import { navigationType } from '../../common/propTypes';
import useAppCheck from '../../hooks/useAppCheck';

const Home = ({ navigation }) => {
  const isDarkMode = useColorScheme() === 'dark';
  const { hasLogin } = useContext(AppContext);
  const [loading, setLoading] = useState(false);

  const isNewVersion = useAppCheck();

  const backgroundStyle = {
    backgroundColor: isDarkMode ? Colors.darker : Colors.lighter,
  };

  useEffect(() => {
    const init = async () => {
      await onHasLogin();
    };
    if (isNewVersion !== null) {
      init();
    }
  }, [isNewVersion]);

  const onHasLogin = async () => {
    setLoading(true);
    try {
      const is = await hasLogin();
      if (!is) {
        navigation.push('Login');
      }
      setLoading(false);
      return is;
    } catch (e) {
      setLoading(false);
      return false;
    }
  };

  const openURL = useCallback(async () => {
    const url = `https://www.pgyer.com/app/installUpdate/ff6f09f5492f701ab12fe82
    f42630f07?sig=SSkXB3Ct9M9Pph0oCS8bYPl3SC%2BULNnXPxR%2Fdn
    8eqssrOMW2JjwgJae62w83rCLn&forceHttps=`;
    const supported = await Linking.canOpenURL(url);

    if (supported) {
      await Linking.openURL(url);
    } else {
      Toast.fail('url出错');
    }
  }, []);

  return (
    <SafeAreaView style={backgroundStyle}>
      <StatusBar barStyle={isDarkMode ? 'light-content' : 'dark-content'} />
      <ScrollView
        contentInsetAdjustmentBehavior="automatic"
        style={backgroundStyle}
      >
        <Header />

        <View>
          <Text>{loading ? 'loading' : ''}</Text>
        </View>

        <Button onPress={() => navigation.navigate('Login')}>go</Button>

        <Button onPress={openURL}>openURL</Button>

      </ScrollView>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
});

Home.propTypes = {
  navigation: navigationType.isRequired,
};

export default Home;
