/**
 * 对象转成url参数
 * @param obj //
 * @returns {string} // key1=value1&key2=value2
 */
export const objToUrlParam = (obj) => {
  const params = [];
  Object.keys(obj).forEach((key) => {
    const value = obj[key];
    params.push(`${key}=${value}`);
  });
  return params.join('&');
};

/**
 * 对象转成FormData
 * @param obj
 * @returns {FormData}
 */
export const objToFormData = (obj) => {
  const formData = new FormData();
  Object.keys(obj).forEach((key) => {
    const value = obj[key];
    formData.append(key, value);
  });
  return formData;
};
