import React, { useReducer, useCallback, useEffect } from 'react';
import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { View } from 'react-native';
import Home from './pages/home';
import Login from './pages/login';
import { AppContext, AppContextValue } from './common/AppContext';
import { Button, Provider, Toast } from '@ant-design/react-native';


const Stack = createNativeStackNavigator();

const App = () => {
  const [state, dispatch] = useReducer(
    (prevState, action) => {
      switch (action.type) {
        case 'LOGIN':
          return {
            ...prevState,
            isLogin: true,
          };
        case 'LOGOUT':
          return {
            ...prevState,
            isLogin: false,
          };
        default: {
          return { ...prevState };
        }
      }
    },
    {
      isLoading: false,
      isLogin: false,
    },
  );

  const onLogin = useCallback(async () => dispatch({ type: 'LOGIN' }), []);

  const hasLogin = () => new Promise((resolve) => {
    setTimeout(() => resolve(false), 2000);
  });

  return (
    // eslint-disable-next-line react/jsx-filename-extension
    <AppContext.Provider
      value={{
        ...AppContextValue, isLogin: state.isLogin, onLogin, hasLogin,
      }}
    >
      <Provider>
        <NavigationContainer>
          <Stack.Navigator screenOptions={{ headerShown: true }}>
            <>
              <Stack.Screen
                name="Home"
                component={Home}
                headerShown={false}
                options={{ title: '首页', headerShown: false }}
              />
              <Stack.Screen
                name="Login"
                component={Login}
                options={{
                  title: '登录',
                  headerLeft: () => (state.isLogin ? null : <View />),
                  animationTypeForReplace: state.isLogin ? 'pop' : 'push',
                }}
              />
            </>
          </Stack.Navigator>
        </NavigationContainer>
      </Provider>
    </AppContext.Provider>
  );
};

export default App;
